@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');

@layer base { 
    body {
        @apply font-[Raleway];
    }
    li {
        @apply px-4 cursor-pointer;
    }
}

.content-div {
    @apply bg-cover bg-center;
    background-repeat: no-repeat;
    height: 250px;
}

.content-div:hover {
    background-image: linear-gradient(
        to right,
        rgba(222, 186, 176, 0.954),
        hsla(11, 64%, 85%, 0.885)   
    )!important;
}

/* Ensure the container expands downwards and remains stationary */
.container {
    @apply flex flex-col;
    min-height: 100vh; /* Ensure container fills the viewport height */
}

.section {
    @apply flex-grow; /* Allow sections to grow */
    min-height: 0; /* Prevent collapse */
    padding: 1rem; /* Add padding to sections for spacing */
}

.section-padding {
    @apply py-16; /* Consistent vertical spacing */
}

/* Apply padding to all sections */
#work, #project, #contact, #about {
    @apply section-padding;
}
